import React from 'react';

const  NewsBlock =(props)=> {
    
        let {newsTitle,newsShortDesc,newsImg,detailNewsUrl,newsTime,author,source} = props;
        return <>
       
        <div className="card mb-3">
        <span  style={{left:'80%'}}className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-info">
    {source}
   </span>
        
  <div className="row g-0">
    <div className="col-md-4">
      <img src={newsImg?newsImg:"https://english.cdn.zeenews.com/sites/default/files/2021/10/09/978024-untitled-design-2021-10-09t180806.105.jpg"} className="img-fluid rounded-start" alt="IMG"/>
    </div>
    <div className="col-md-8">
      <div className="card-body">
        <h5 className="card-title">{newsTitle}</h5>
        <p className="card-text">{newsShortDesc}</p>
        <p className="card-text"><small className="text-muted">Author:  {author?author:'unknown'} || Published At: {new Date(newsTime).toGMTString()}</small></p>
        <a href={detailNewsUrl} target ="_blank" rel="noreferrer" className="btn btn-primary">In Detail</a>
        

      </div>
    </div>
  </div>
</div>
       </>
    }
export default NewsBlock