
import './App.css';

import React, { useState } from 'react'
import NavBar from './components/NavBar.js';
import NewsGrid from './components/NewsGrid.js';
import {
  BrowserRouter as Router,
  Switch,
  Route,
 
} from "react-router-dom";
import LoadingBar from 'react-top-loading-bar';



const  App =()=> {
  const noOfBlock=6;
  const apiKey = process.env.REACT_APP_NEWS_API;

  const [progress,setProgress] = useState(0);
  

  
    return (
      <div>
    <Router>
        <NavBar/>
        <LoadingBar
        height={4}
        color='#f11946'
        progress={progress} 
      />
        <Switch>
        <Route exact key="general" path="/">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="General" noOfBlock={noOfBlock}/>
          </Route> 
          <Route exact key="general" path="/general">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="General" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="business" path="/business">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Business" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="entertainment" path="/entertainment">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Entertainment" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="sports" path="/sports">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Sports" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="health" path="/health">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Health" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="science" path="/science">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Science" noOfBlock={noOfBlock}/>
          </Route>

          <Route exact key="technology" path="/technology">
          <NewsGrid setProgress={setProgress} apiKey={apiKey} country="in"  category ="Technology" noOfBlock={noOfBlock}/>
          </Route>
        </Switch>
        </Router>
        </div>
    )
  }

  export default App