import React,{useEffect, useState}  from 'react';

import NewsBlock from '../components/NewsBlock.js';
import InfiniteScroll from "react-infinite-scroll-component";

import Loading from '../components/Lodingscreen.js';
import PropTypes from 'prop-types';
const NewsGrid =(props)=>{

    // static defaultProps = {
    //     country: 'in',
    //     noOfBlock: 45,
    //     category: 'general'
    // }

    const [articles,setArticles] = useState([]);
    const [loading, setLoading] = useState(true);
    const [page, setPage] = useState(1);
    const [totalResults, setTotalResults] = useState(0);

   

    
 
 const  fetchData = async ()=> {
      props.setProgress(10);
        let url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=1&pageSize=${props.noOfBlock}`;
        let data = await fetch(url);
       props.setProgress(30);
        let personData = await data.json();
        props.setProgress(70);
        console.log(personData);
       
        setArticles(personData.articles);
        setTotalResults(personData.totalResults);
        setLoading(false)

        props.setProgress(100);
    }
    useEffect(() => {
        fetchData(); 
        // eslint-disable-next-line
    }, [])

    const fetchMoreData = async () => {
        const url = `https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=${props.apiKey}&page=${page+1}&pageSize=${props.noOfBlock}`;
        setPage(page+1)
    //   this.setState({loading:true})
      let data = await fetch(url);
      let personData = await data.json();
      console.log(personData);
      setArticles(articles.concat(personData.articles))
      setTotalResults(personData.totalResults)
      
      };

 

        return <>
                <h1 className="text-center" style={{marginTop:'78px', marginBottom:'1rem'}}>Top News on {props.category} </h1>
                {loading && <Loading/>}
                <InfiniteScroll
                    dataLength={articles.length}
                    next={fetchMoreData}
                    hasMore={articles.length !== totalResults}
                    loader={<Loading/>}

                    style={{overflow:'unset'}}
                    >
        <div className="container">

                
                    <div className="row">
            {articles.map((element) => {
                return <div key={element.url}>
                <NewsBlock newsTitle={element.title?element.title: ''} newsShortDesc={element.description?element.description: ''} newsImg={element.urlToImage} detailNewsUrl={element.url} author={element.author} source={element.source.name} newsTime={element.publishedAt} />
                </div>
            })}

                </div>
            </div>
        </InfiniteScroll>



        </>
    }
    NewsGrid.propTypes = {
        country: PropTypes.string,
        noOfBlock: PropTypes.number,
        category: PropTypes.string

    }
    export default NewsGrid